import React, {Component} from 'react'
import {connect} from 'react-redux'
import {bindActionCreators} from 'redux'
import toJS from 'components/HOC/toJs'
import {Link} from 'react-router-dom'
import {Select, AmountFormat} from 'common/uiElements'
import Slider from 'react-rangeslider'



function mapStateToProps(state, ownProps) {
    return {}
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({}, dispatch);
}

@connect(mapStateToProps, mapDispatchToProps)
@toJS
export default class extends Component {
    constructor (props, context) {
        super(props, context);
        this.state = {
            value: 20000
        }
    }

    handleChangeStart = () => {
        console.log('Change event started')
    };

    handleChange = value => {
        this.setState({
            value: value
        })
    };

    handleChangeComplete = () => {
        console.log('Change event completed')
    };

    render() {
        const { value } = this.state;

        return (
            <div>

                <div className="title_panel">
                    <Link to={'/'} className="button small light orange f_right"> </Link>
                </div>

                <div className="tabs_flat tabs_flat__h1">
                    <a className="tab tab__active">  </a>
                    <a className="tab"> </a>
                </div>

                <h3 className='opt_title'>  </h3>

                <div className='opt_box'>
                    <div className='opt_label'> </div>
                    <Select
                        className='w30'
                        options={[
                            {label: ' '},
                            {label: ' .'},
                            {label: ' '}
                        ]}
                    />
                </div>

                <div className='opt_box'>
                    <div className='opt_label'> </div>
                    <Slider
                        min={0}
                        max={100000}
                        value={value}
                        onChangeStart={this.handleChangeStart}
                        onChange={this.handleChange}
                        onChangeComplete={this.handleChangeComplete}
                    />
                    <div className='opt_slider_value'><AmountFormat value={value} /></div>
                </div>

            </div>
        )

    }
}


